.header {
    max-width: 880px;
    margin: 0 auto;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
    margin-top: 45px;
    margin-bottom: 40px;
}

@media screen and (max-width: 425px) {
    .header {
        max-width: 100%;
        margin-top: 28px;
        margin-bottom: 38px;
    }
}